.container {
  padding-top: 2rem;
  padding-bottom: 2rem;
}

.button-group {
  display: flex;
  justify-content: space-between;
  margin-bottom: 1rem;
}

.button {
  flex: 0 1 49%;
  font-size: 1.5rem;
}

.button:focus, .button:hover {
  background-color: #9b4dca;
  border-color: #9b4dca;
}